import React, {useMemo} from 'react';

import {Button} from '@material-ui/core';
import Label from '../../../components/Label';
import Value from '../../../components/Value';
import useEarnings from '../../../hooks/useEarnings';
import useHarvest from '../../../hooks/useHarvest';
import useCompound from '../../../hooks/useCompound';
import {getDisplayBalance} from '../../../utils/formatBalance';
import TokenSymbol from '../../../components/TokenSymbol';
import {Bank} from '../../../polydoge-finance';
import usePDDollarStats from '../../../hooks/usePDDollarStats';
import usePDShareStats from '../../../hooks/usePDShareStats';

const Harvest = ({bank}) => {
  const earnings = useEarnings(bank.contract, bank.earnTokenName, bank.poolId);
  const grapeStats = usePDDollarStats();
  const tShareStats = usePDShareStats();
  const tokenStats = bank.earnTokenName === 'PDDOLLAR' ? grapeStats : tShareStats;
  
  const tokenPriceInDollars = useMemo(
    () => (tokenStats ? Number(tokenStats.priceInDollars).toFixed(2) : null),
    [tokenStats],
  );
  const earnedInDollars = (Number(tokenPriceInDollars) * Number(getDisplayBalance(earnings))).toFixed(2);
  const { onReward } = useHarvest(bank);
  const { onCompound } = useCompound(bank);

  return (
    <div className="mainContainer">
      <div className="baseCard harvestCard">
        <div className="label">reward</div>
        <div className="containerInfoCard">
          <TokenSymbol symbol={bank.earnTokenName} />
          <div className="harvestValue">
            <Value value={Number(getDisplayBalance(earnings)).toFixed(2)} />
          </div>
          <div className="harvestValueEarned">
            <Label text={`â‰ˆ $${earnedInDollars}`} variant="nodey" />
              <div>{bank.earnTokenName} Earned</div>
          </div>
        </div>
        <div className='harvestButtonsContainer'>
          <Button
            onClick={onReward}
              disabled={earnings.eq(0)}
              className={earnings.eq(0) ? 'claimHarvestOff' : 'claimHarvestOn'}
          >
            Claim Reward
          </Button>
          {/* {bank.earnTokenName === 'PDDOLLAR' ? (
            <Button
              onClick={onCompound}
              disabled={earnings < 50 * 1e18}
              className={earnings < 50 * 1e18 ? 'claimHarvestOff' : 'claimHarvestOn'}
            >
              Compound {(earnings / (50 * 1e18)) | 0} Nodes
            </Button>
          ) : (
            <Button
              onClick={onCompound}
              disabled={earnings < 50 * 1e18}
              className={earnings < 50 * 1e18 ? 'claimHarvestOff' : 'claimHarvestOn'}
            >
              Compound {(earnings / (50 * 1e18)) | 0} Nodes
            </Button>
          )} */}
        </div>
      </div>
    </div>
  );
};

export default Harvest;